.viewbox-container {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  z-index: 700;
}

.viewbox-body {
  position: absolute;
  top: 50%;
  left: 50%;
  background: #fff;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  border-radius: 10px;
  -webkit-box-shadow: 0 0 10px rgba(0, 0, 0, 0.6);
  -moz-box-shadow: 0 0 10px rgba(0, 0, 0, 0.6);
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.6);
  overflow: auto;
}

.viewbox-header {
  margin: 10px;
}

.viewbox-content {
  margin: 10px;
  width: 300px;
  height: 300px;
}

.viewbox-footer {
  margin: 10px;
}

.viewbox-content .viewbox-image {
  width: 100%;
  height: 100%;
}

/* buttons */
.viewbox-button-default {
  cursor: pointer;
  height: 64px;
  width: 64px;
  fill: #dadada !important;
}
.viewbox-button-default > svg {
  width: 100%;
  height: 100%;
  background: inherit;
  fill: #dadada;
  pointer-events: none;
  transform: translateX(0px);
}
.viewbox-button-default:hover {
  fill: #fff;
}

.viewbox-button-close {
  position: absolute;
